.wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    max-width: 1440px;
    margin: 50px auto;
}

.content {
    min-width: 700px;
    padding: 10px 20px; 
    background-color: rgb(204, 204, 204);
    border-radius: 10px;
    border: 2px solid black;
}

.header_actions {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;

}

.btn {
    background-color: aquamarine;
    padding: 10px 16px;
    color: rgb(83, 83, 83);
    width: 100px;
    border-radius: 5px;
    border: 1px solid black;
    text-align: center;
    white-space: nowrap;
    cursor: pointer;
}

.inputText {
    height: 35px;
    padding: 0px 5px;
    border-radius: 5px;
    border: 1px solid black;
    text-align: center;
}
.todoInput{
    width: 100%;
}

.block_item {
    display: flex;
    align-items: center;
    width: 70%;
    gap: 20px;
    border: 2px solid black;
    border-radius: 5px;
    padding: 0 10px;
    margin: 15px auto;
}

.new_btn_block{
    background-color: aquamarine;
    width: 40px ;
    height: 30px;
    color: rgb(83, 83, 83);
    border-radius: 5px;
    padding: 5px;
    cursor: pointer;
}

.active{
    background-color: rgb(167, 167, 167);
}

.input_value{
    width: 63%;
    background-color: white;
    border-radius: 5px;
    padding: 20px;
    word-wrap: break-word;
}

.block_item_info{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 20px;
}

.show{
    width: 150px;
}
.header_info{
    width: 100%;
    display: flex;
    align-items: center;
    gap: 10px;
    justify-content: space-between;
}
